import {StaticDependencyInjector} from "@util/static-dependency-injector";
import {PersistentStorageService} from "@service/browser-storage.service";
import {CardContainer, DragCardPosition} from "./drag-card-position";
import {VsanUiUtils} from "@util/vsan-util";
import {CardView} from "@component/drag-and-drop/card-view";

/**
 * Util methods for storing and fetching the positioning state of draggable cards from the browser cache.
 */
export class DraggableCardsUtil {
   private static browserStorageService: PersistentStorageService =
         StaticDependencyInjector.get<PersistentStorageService>(PersistentStorageService);

   static getCardsPosition(view: CardView): Map<string, DragCardPosition> {
      const cardPositionsBrowserInfo: {value: any[]} = DraggableCardsUtil.browserStorageService.get(view.cardName);
      if (cardPositionsBrowserInfo && !VsanUiUtils.isEmpty(cardPositionsBrowserInfo.value)) {
         // Cards are already initialized via browser storage, convert the data to Map
         return cardPositionsBrowserInfo.value.reduce((map, obj) => {
            map.set(obj[0], obj[1]);
            return map;
         }, new Map<string, DragCardPosition>());
      }
      return null;
   }

   static storeCardsPosition(view: CardView, value: Map<string, DragCardPosition>): void {
      if (!value) {
         return;
      }
      this.browserStorageService.set(view.cardName, {value: Array.from(value.entries())});
   }
}