import {Component, Input, QueryList} from "@angular/core";
import {DraggableCardDirective} from "./draggable-card.directive";
import {CardContainer, DragCardPosition} from "./drag-card-position";
import {DraggableCardsUtil} from "./draggable-cards-util";
import {CdkDragDrop, moveItemInArray, transferArrayItem} from "@angular/cdk/drag-drop";
import {CardView} from "@component/drag-and-drop/card-view";
import {ResizedEvent} from "angular-resize-event";

@Component({
  selector: "vsan-drag-and-drop",
  templateUrl: "drag-and-drop.html",
  styleUrls: ["drag-and-drop.scss"],
})
export class DragAndDropComponent {

  private static readonly MIN_WIDTH_NO_WRAP: number = 1000;

  @Input()
  view: CardView;

  @Input()
  cardTemplates: QueryList<DraggableCardDirective>;

  leftSideCards: DraggableCardDirective[];
  rightSideCards: DraggableCardDirective[];

  wrapCards: boolean;

  // Holds map with card positions for each card. They are stored/read from the browser cache.
  private cardPositions: Map<string, DragCardPosition>;

  ngOnInit(): void {
    this.cardPositions = DraggableCardsUtil.getCardsPosition(this.view);
    if (!this.cardPositions) {
       this.cardPositions = this.view.getDefaultCardsPositions();
    }
    this.showCards();
  }

  onResized(event: ResizedEvent): void {
    this.wrapCards = event.newWidth < DragAndDropComponent.MIN_WIDTH_NO_WRAP;
  }

  private showCards(): void {
    // Timeout is needed as the isVisible property is not initialized at the time
    // this method gets called, which causes the checks to always fail.
    setTimeout(() => {
      // Using tmp arrays to avoid multiple change detection happening for the drag lists
      // as we add service templates to them
      const leftSideCardsTmp: DraggableCardDirective[] = [];
      const rightSideCardsTmp: DraggableCardDirective[] = [];
      // once visualized, the cards and their positions are cached in a map. If new cards are added / old cards are
      // renamed, they can not be found in the cache map. Add them here and update the cached map at the end.
      const newCards: DraggableCardDirective[] = [];
      this.cardTemplates
            .filter(cardTemplate => cardTemplate.isVisible)
            .forEach(cardTemplate => {
              const positionInfo: DragCardPosition = this.cardPositions.get(cardTemplate.name);
              if (!positionInfo) {
                newCards.push(cardTemplate);
              } else if (positionInfo.container === CardContainer.LEFT) {
                leftSideCardsTmp.splice(positionInfo.index, 0, cardTemplate);
              } else {
                rightSideCardsTmp.splice(positionInfo.index, 0, cardTemplate);
              }
            });

      this.leftSideCards = leftSideCardsTmp;
      this.rightSideCards = rightSideCardsTmp;

      if (newCards.length) {
        newCards.forEach(card => {
          if (this.leftSideCards.length <= this.rightSideCards.length) {
            this.leftSideCards.push(card);
          } else {
            this.rightSideCards.push(card);
          }
        });
        this.updateCardsPositionInfo();
      }
    });
  }

  drop(event: CdkDragDrop<string[]>) {
    if (event.previousContainer === event.container) {
      moveItemInArray(event.container.data, event.previousIndex, event.currentIndex);
    } else {
      transferArrayItem(event.previousContainer.data,
            event.container.data,
            event.previousIndex,
            event.currentIndex);
    }
    this.updateCardsPositionInfo();
  }

  private updateCardsPositionInfo(): void {
    this.leftSideCards.forEach((card, index) => {
      this.cardPositions.set(card.name, new DragCardPosition(CardContainer.LEFT, index));
    });

    this.rightSideCards.forEach((card, index) => {
      this.cardPositions.set(card.name, new DragCardPosition(CardContainer.RIGHT, index));
    });
    DraggableCardsUtil.storeCardsPosition(this.view, this.cardPositions);
  }
}
